class StateManager {
  constructor(game) {
    this.game = game;
    this.current = null;
    this.states = {};
  }

  add(key, state) {
    this.states[key] = state;
  }

  remove(key) {
    delete this.states[key];
  }

  start(key) {
    this.current = this.states[key];
    this.current.init();
  }
}

export default StateManager;


